import WhatsApp from './WhatsApp/WhatsApp'
import Unsubscribe from './Unsubscribe/Unsubscribe'

import { UNSUB_ROUTE, WHATSAPP_ROUTE } from 'constants/url'

const PAGES_DATA = {
  WHATSAPP: {
    Component: WhatsApp,
    link: WHATSAPP_ROUTE,
  },
  UNSUBCRIBE: {
    Component: Unsubscribe,
    link: UNSUB_ROUTE,
  },
}

const PAGES = Object.values(PAGES_DATA)
const CURRENT_PAGE = process.env.REACT_APP_PAGE as keyof typeof PAGES_DATA
const START_PAGE = PAGES_DATA[CURRENT_PAGE]?.link

export { START_PAGE }
export default PAGES
