import { FC } from 'react'
import cx from 'classnames'

import Button from 'components/Button/Button'
import Table from './Table/Table'
import useTable from 'hooks/useTable'
import { headers, reasons as rows } from './constants'
import styles from './Unsubscribe.module.scss'

const title = 'Вы уверены, что хотите отписаться от рассылки?'
const allIds = headers.flatMap((header) => rows.map((row) => `${row.id}_${header.id}`))

const Unsubscribe: FC = () => {
  const { toggleCheck, isItChecked, checkedAll, toggleAllChecks } = useTable(allIds)

  const email = 'e.solonin@onpoint.com'

  const handleSave = () => {
    console.log('save')
  }

  const handleCancel = () => window.close()

  return (
    <div className={styles.pageWrap}>
      <p className={'title'}>{title}</p>
      <p className={cx(styles.email)}>{email}</p>

      <Table
        toggleCheck={toggleCheck}
        isItChecked={isItChecked}
        checkedAll={checkedAll}
        toggleAllChecks={toggleAllChecks}
      />

      <div className={styles.btnsWrap}>
        <Button onClick={handleSave}>Сохранить</Button>
        <Button secondary onClick={handleCancel}>
          Отменить
        </Button>
      </div>
    </div>
  )
}

export default Unsubscribe
