import { FC } from 'react'
import cx from 'classnames'

import styles from './CheckBox.module.scss'
import { IconCheck } from 'assets/icons'

interface ICheckBox {
  id: string
  isChecked: boolean
  toggleCheck: (e: React.MouseEvent<HTMLElement>) => void
}

const CheckBox: FC<ICheckBox> = ({ id, isChecked, toggleCheck }) => {
  return (
    <div className={cx(styles.wrap)} onClick={toggleCheck} data-id={id}>
      <div
        className={cx(styles.check, {
          [styles.checked]: isChecked,
        })}
      >
        {isChecked && <IconCheck />}
      </div>
    </div>
  )
}

export default CheckBox
