import { FC } from 'react'
import cx from 'classnames'
import styles from './Table.module.scss'

interface ICell {
  main?: boolean
  head?: boolean
  check?: boolean
}

export const Cell: FC<ICell> = ({ children, main, head, check }) => {
  return (
    <div
      className={cx(styles.cell, styles.mainCell, {
        [styles.headCell]: head,
        [styles.mainCell]: main,
        [styles.checkCell]: check,
      })}
    >
      {children}
    </div>
  )
}
