import { FC } from 'react'

import CheckBox from 'components/CheckBox/CheckBox'
import { Cell } from './Cell'
import { headers, reasons as rows } from '../constants'

import styles from './Table.module.scss'

interface ITable {
  checkedAll: boolean
  toggleAllChecks: () => void
  toggleCheck: (e: React.MouseEvent<HTMLElement>) => void
  isItChecked: (id: string | undefined) => boolean
}

const Table: FC<ITable> = ({ toggleCheck, isItChecked, checkedAll, toggleAllChecks }) => {
  return (
    <div className={styles.table}>
      <div className={styles.head}>
        <div className={styles.row}>
          <Cell main head />
          {headers.map((header, index) => (
            <Cell head key={index}>
              {header.label}
            </Cell>
          ))}
        </div>
      </div>
      <div className={styles.tbody}>
        {rows.map((row, index) => {
          return (
            <div className={styles.row} key={index}>
              <Cell main>{row.label}</Cell>
              {headers.map((header) => {
                const id = `${row.id}_${header.id}`
                const isChecked = isItChecked(id)
                return (
                  <Cell check key={id}>
                    <CheckBox id={id} toggleCheck={toggleCheck} isChecked={isChecked} />
                  </Cell>
                )
              })}
            </div>
          )
        })}
      </div>
      <div className={styles.foot}>
        <div className={styles.footRow}>
          <CheckBox id="0" toggleCheck={toggleAllChecks} isChecked={checkedAll} />
          <span className={styles.label} onClick={toggleAllChecks}>
            Отписаться от всех уведомлений
          </span>
        </div>
      </div>
    </div>
  )
}

export default Table
