import { FC, useState, useRef, useEffect, useCallback } from 'react'
import cx from 'classnames'

import PortalWrap from 'containers/PortalWrap'

import useClickOutside from 'hooks/useClickOutside'

import styles from './DropDown.module.scss'
import { IOption } from 'types'

interface DropDown {
  triggerNode: React.ReactChild
  options: IOption[]
  mouseLeave?: boolean
  handleOptionPick: (id: string) => void
}

const gap = 10

const DropDown: FC<DropDown> = ({ triggerNode, mouseLeave, options, handleOptionPick }) => {
  const [isMenuOpened, setMenuOpened] = useState(false)
  const [position, setPosition] = useState({ x: 0, y: 0 })
  const triggerRef = useRef<HTMLElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)

  const togglePopup = (event: any) => {
    event.stopPropagation()
    setMenuOpened(!isMenuOpened)
  }

  const closePopup = (event: any) => {
    event.stopPropagation()
    setMenuOpened(false)
  }

  const triggerRect = triggerRef.current?.getBoundingClientRect()

  const calcDDPosition = useCallback(() => {
    if (!triggerRect) return position
    const containerRect = containerRef.current?.getBoundingClientRect()
    if (!containerRect) return position

    const pos = {
      y:
        containerRect.height + triggerRect.top > window.innerHeight * 0.95
          ? -triggerRect.height - gap + triggerRect.bottom + window.scrollY - containerRect.height
          : triggerRect.height + window.scrollY + triggerRect.top + gap,

      x:
        containerRect.width + triggerRect.left > window.innerWidth * 0.95
          ? triggerRect.right - containerRect.width
          : triggerRect.left,
    }

    return pos
  }, [position, triggerRect])

  useClickOutside(isMenuOpened, containerRef, closePopup)

  useEffect(() => {
    if (!isMenuOpened) return

    setPosition(calcDDPosition())
  }, [isMenuOpened])

  return (
    <>
      <div
        className={cx(styles.triggerContainer)}
        onClick={togglePopup}
        ref={triggerRef as React.RefObject<HTMLDivElement>}
      >
        {triggerNode}
      </div>
      {isMenuOpened && (
        <PortalWrap>
          <div
            className={styles.taskHoverBlock}
            onMouseDown={(e) => e.preventDefault()}
            onMouseLeave={() => mouseLeave && setMenuOpened(false)}
            onClick={closePopup}
            style={{ top: position.y, left: position.x }}
            tabIndex={0}
            ref={containerRef}
          >
            <div className={styles.container}>
              {options.map((option, index) => (
                <button
                  className={cx(styles.element, 'text')}
                  onClick={() => handleOptionPick(option.id)}
                  key={index}
                >
                  {option.label}
                </button>
              ))}
            </div>
          </div>
        </PortalWrap>
      )}
    </>
  )
}

export default DropDown
