import { FC } from 'react'
import cx from 'classnames'

import styles from './Button.module.scss'

export interface IButton extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  modificator?: string | string[]
  secondary?: boolean
}

const Button: FC<IButton> = ({ onClick, modificator, children, type, secondary, ...props }) => {
  return (
    <button
      className={cx(styles.button, 'text_hl', modificator, {
        [styles.theme_secondary]: secondary,
      })}
      onClick={onClick}
      type={type}
      {...props}
    >
      {children}
    </button>
  )
}

export default Button
