import cx from 'classnames'
import Button from 'components/Button/Button'
import DropDown from 'components/DropDown/DropDown'
import { TRecipient, TStatus } from 'pages/WhatsApp/types'
import { FC } from 'react'

import styles from './OneRecipient.module.scss'

const options = [
  {
    label: 'Сменился номер',
    id: '0',
  },
  {
    label: 'Отказался от WA',
    id: '1',
  },
  {
    label: 'Больше не посещается',
    id: '2',
  },
  {
    label: 'Не работает',
    id: '3',
  },
]

interface IOneRec {
  recipient: TRecipient
  text: string
  updateStatus: (newStatus: TStatus) => void
}

const OneRecipient: FC<IOneRec> = ({ recipient, updateStatus, text }) => {
  const { name, specialty, status, phone } = recipient

  const isSendable = status === 'new' || status === 'old'
  const statusToRender = status === 'sent' ? 'отправлено' : 'отменено'

  const handleSend = async () => {
    const textEncoded = encodeURIComponent(text)
    window.open(`https://wa.me/${phone}?text=${textEncoded}`, '_blank')

    updateStatus('sent')
  }

  const handleChange = async () => {
    updateStatus('old')
  }

  const handleOptionPick = async (id: string) => {
    updateStatus('unsub')
  }

  const RightButtons = () => {
    if (isSendable)
      return (
        <>
          <Button onClick={handleSend}>Отправить</Button>
          <DropDown
            options={options}
            handleOptionPick={handleOptionPick}
            triggerNode={<Button secondary>Отменить</Button>}
          />
        </>
      )

    return (
      <>
        <p className={cx('text', styles.status)}>Статус: {statusToRender}</p>
        <Button onClick={handleChange} secondary>
          Изменить
        </Button>
      </>
    )
  }

  return (
    <div
      className={cx(styles.wrap, {
        [styles.wrapGray]: !isSendable,
      })}
    >
      <div
        className={cx(styles.leftBlock, {
          [styles.leftBlockGray]: !isSendable,
        })}
      >
        <div className={'text_hl'}>{name}</div>
        <div className={cx(styles.specialty, 'text')}>{specialty}</div>
      </div>

      <div className={styles.rightBlock}>
        <RightButtons />
      </div>
    </div>
  )
}

export default OneRecipient
