import { FC, useState } from 'react'
import Message from './components/Message/Message'
import Recipients from './components/Recipients/Recipients'
import { recipients } from './recipients'
import { TRecipient, TStatus } from './types'

import styles from './WhatsApp.module.scss'

const name = 'WhatsApp рассылка 19.12.2022'
const text =
  'Добрый день! Приглашаем вас принять участие в онлайн-конгрессе, посвященному дню ВПЧ. Регистрация по ссылке http://www.vpch.day.ru'

const WhatsApp: FC = () => {
  const [recipientsState, setRecipientsState] = useState(recipients as TRecipient[])

  const updateStatus = (index: number, newStatus: TStatus) => {
    setRecipientsState((prev) => [
      ...prev.slice(0, index),
      { ...prev[index], status: newStatus },
      ...prev.slice(index + 1),
    ])
  }

  return (
    <div className={styles.page}>
      <div className={styles.pageWrap}>
        <div className={'title'}>{name}</div>
        <Message text={text} />
        <Recipients recipients={recipientsState} updateStatus={updateStatus} text={text} />
      </div>
    </div>
  )
}

export default WhatsApp
